import { Button } from '@/components/ui/button'
import Link from 'next/link'
import Image from 'next/image'

const MyProfile = () => {
  return (
    <div className='flex-1 flex flex-col'>
      <div className='flex justify-between items-center py-8'>
        <h1 className='font-bold text-4xl'>สูตรอาหารของฉัน</h1>
        <Link href={'/create-recipe'}>
          <Button className='border text-primary-500' variant='ghost'>
            <Image
              src='icons/Edit.svg'
              width={12}
              height={12}
              alt='edit logo'
            />
            แก้ไขโปรไฟล์
          </Button>
        </Link>
      </div>
      <div className='flex-1 border flex justify-center items-center'>
        <div>
            <div className='bg-slate-200 rounded-full'></div>
            <div className='bg-slate-200 rounded-full'>Apinut</div>
            <Form {...form}>
        <form
          className='flex flex-col gap-y-4'
          onSubmit={form.handleSubmit(onSubmit)}
        >
          <FormField
            control={form.control}
            name='name'
            render={({ field }) => (
              <FormItem>
                <FormLabel>ชื่อเมนู</FormLabel>
                <FormControl>
                  <Input
                    className='rounded-[20px]'
                    placeholder='กรอกชื่อเมนูอาหารของฉัน'
                    {...field}
                  />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />          
        </form>
      </Form>
        </div>
      </div>
    </div>
  )
}

export default MyProfile
